<template>
    <div class="sign-page">
        <div class="overflow-container">
            <Calendar :mapData="mapData" @cellClick="selectDate" @prevClick="changedMonth" @nextClick="changedMonth"></Calendar>
            <div class="sign" v-if="today.year == selectedDate.year && today.month == selectedDate.month && today.day == selectedDate.day">
                <a class="btn" @click="signIn">打卡签到</a>
            </div>
            <table v-show="selectedDateRecords.length">
                <tr>
                    <th>ID</th>
                    <th>时间</th>
                </tr>
                <tr v-for="(item,index) of selectedDateRecords" :key="index">
                    <td>{{index+1}}</td>
                    <td>{{new Date(item.timestamp).Format("yyyy-MM-dd hh:mm:ss")}}</td>
                </tr>
            </table>
        </div>
        <TabBar></TabBar>
    </div>
</template>
<script>
import Calendar from "../../components/Calendar/index.vue";
import TabBar from "../../components/TabBar/index.vue";
import { getYMD } from "../../util";
import signRecordModel from "../../util/signRecordModel";
export default {
    name: "sign-page",
    components: { TabBar, Calendar },
    data() {
        let today = getYMD();
        return {
            today,
            selectedDate: today,
            currMonthRecords: []
        };
    },
    computed: {
        mapData() {
            return this.currMonthRecords.map(item => {
                return {
                    day: item.day,
                    month: item.month,
                    year: item.year,
                    data: item
                };
            });
        },
        selectedDateRecords() {
            return this.currMonthRecords.filter(item => {
                return (
                    item.year == this.selectedDate.year &&
                    item.month == this.selectedDate.month &&
                    item.day == this.selectedDate.day
                );
            });
        }
    },
    mounted() {
        this.changedMonth(this.today);
    },
    beforeMount() {},
    destroyed() {},
    methods: {
        async changedMonth(ym) {
            this.currMonthRecords = await this.getHighlightedDates(ym);
        },
        async getHighlightedDates(YMD) {
            return await signRecordModel.getRecordsByYM(YMD);
        },
        signIn() {
            new signRecordModel({
                timestamp: Date.now()
            }).save();
            this.changedMonth(this.today);
        },
        selectDate(date) {
            this.selectedDate = date;
        }
    }
};
</script>
<style lang="scss" >
.sign-page {
    .sign {
        margin: 10px auto;
        text-align: center;
        .btn {
            display: inline-block;
            width: 200px;
            height: 80px;
            line-height: 80px;
            border-radius: 8px;
            font-size: 30px;
            background-color: #fff;
            border: 1px solid #ddd;
        }
    }

    table {
        width: 100%;
        background-color: #fff;
        th,
        td {
            text-align: center;
            width: 50%;
            padding: 10px;
        }
    }
}
</style>
