<template>
    <div class='calendar-wrap'>
        <div class="calendar-header">
            <span class='left' @click='prevClick'>上一月</span>
            <div class='title'>{{headerText}}</div>
            <span class='right' @click='nextClick'>下一月</span>
        </div>
        <div class='calendar-body'>
            <div class='row-header'>
                <div class='cell0'>日</div>
                <div class='cell1'>一</div>
                <div class='cell2'>二</div>
                <div class='cell3'>三</div>
                <div class='cell4'>四</div>
                <div class='cell5'>五</div>
                <div class='cell6'>六</div>
            </div>
            <div class='row' v-for="(week,i) of weeks" :key="i">
                <div @click='cellClick(day)' :class="getDayClass(day)" v-for="(day,ii) of week" :key="ii">
                    <span>{{day.day}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { getTotalDaysArr } from "../../util/index";
export default {
    name: "Calendar",
    props: {
        defaultDate: {
            type: Number,
            default: Date.now()
        },
        headerFmt: {
            type: String,
            default: "yyyy年M月"
        },
        mapData: {
            type: Array,
            default: () => []
        }
    },
    data() {
        let date = null;
        const { defaultDate } = this.$props;
        if (
            typeof defaultDate === "string" ||
            typeof defaultDate === "number"
        ) {
            date = new Date(defaultDate);
        } else {
            date = defaultDate;
        }
        return {
            activeItem: {},
            year: date.getFullYear(),
            month: date.getMonth() + 1
        };
    },
    computed: {
        headerText() {
            const { year, month } = this.$data;
            return new Date(year, month - 1, 1).Format(this.$props.headerFmt);
        },
        totalDays() {
            const { mapData } = this.$props;
            const { year, month } = this.$data;
            let now = Date.now();
            return getTotalDaysArr(year, month).map(day => {
                let dateTime = new Date(year, month - 1, day);
                let item = {
                    year,
                    month,
                    timestamp: dateTime.getTime(),
                    day,
                    dayOfWeek: dateTime.getDay()
                };
                if (now - dateTime > 0 && now - dateTime < 86400000) {
                    item.isToday = true;
                }
                // 附加数据
                if (
                    mapData &&
                    mapData.length &&
                    mapData[0].year == year &&
                    mapData[0].month == month
                ) {
                    for (let i = 0; i < mapData.length; i++) {
                        if (mapData[i].day == day) {
                            item.data = mapData[i].data;
                            mapData.splice(i, 1);
                            break;
                        }
                    }
                }
                return item;
            });
        },
        weeks() {
            let totalDays = this.totalDays;
            var weeks = [];
            if (totalDays) {
                // 由于第一周数据可能不满7个,先截取第一周数据
                var firstSundayIndex = -1;
                totalDays.forEach(function(item, i) {
                    if (item.dayOfWeek == 6 && firstSundayIndex == -1) {
                        firstSundayIndex = i;
                    }
                });
                weeks.push(totalDays.splice(0, firstSundayIndex + 1));
                // 按7的倍数截取剩下的数据
                var len = totalDays.length;
                for (var i = 0; i < len; i += 7) {
                    weeks.push(totalDays.slice(i, i + 7));
                }
            }
            return weeks;
        }
    },
    mounted() {
        // 高亮今日
        if (!this.$data.activeItem.date) {
            this.$data.activeItem = this.totalDays.filter(
                item => item.isToday
            )[0];
            this.$emit("cellClick", this.$data.activeItem);
        }
    },
    methods: {
        prevClick: function() {
            let year = this.$data.year;
            let month = this.$data.month;
            if (month == 1) {
                year = year - 1;
                month = 12;
            } else {
                month--;
            }
            this.$data.year = year;
            this.$data.month = month;
            this.$emit("prevClick", { year, month });
        },
        nextClick: function() {
            let year = this.$data.year;
            let month = this.$data.month;
            if (month == 12) {
                year = year + 1;
                month = 1;
            } else {
                month++;
            }
            this.$data.year = year;
            this.$data.month = month;
            this.$emit("nextClick", { year, month });
        },
        cellClick(day) {
            if (this.$data.activeItem.timestamp == day.timestamp) {
                return;
            }
            this.$data.activeItem = day;
            this.$emit("cellClick", day);
        },
        getDayClass(day) {
            let tmp = {};
            if (day.timestamp == this.$data.activeItem.timestamp) {
                tmp.active = true;
            }
            if (day.data) {
                tmp["has-data"] = true;
            }
            tmp["cell" + day.dayOfWeek] = true;
            return tmp;
        }
    }
};
</script>
<style lang="scss" scoped>
.calendar-wrap {
    display: flex;
    flex-direction: column;
    font-size: 32px;
    background: #fff;
}

.calendar-header {
    text-align: center;
    display: flex;
    height: 100px;
    line-height: 100px;
    background-color: #3389e7;
    color: #fff;
    .title {
        flex: 1;
    }
    .left,
    .right {
        flex: 1;
    }
    .left {
    }
    .right {
    }
}

.calendar-body {
    .row,
    .row-header {
        display: grid;
        grid-template-columns: 13vw auto 13vw auto 13vw auto 13vw auto 13vw auto 13vw auto 13vw;
    }
    .cell0,
    .cell1,
    .cell2,
    .cell3,
    .cell4,
    .cell5,
    .cell6 {
        height: 13vw;
        display: flex;
        align-items: center;
        justify-content: center;
        grid-row: 1;
    }
    .row .cell0,
    .row .cell6 {
        color: red;
    }

    .cell0 {
        grid-column: 1;
    }
    .cell1 {
        grid-column: 3;
    }
    .cell2 {
        grid-column: 5;
    }
    .cell3 {
        grid-column: 7;
    }
    .cell4 {
        grid-column: 9;
    }
    .cell5 {
        grid-column: 11;
    }
    .cell6 {
        grid-column: 13;
    }

    .active {
        border-radius: 50%;
        border: 2px solid #d3d3d3;
    }

    .has-data {
        border-radius: 50%;
        border: 2px solid #d3d3d3;
        background: #00b26a;
        color: #fff;
        border-radius: 50%;
    }
}
</style>
